.feedback {
  width: 100%;
  background-color: $color-deep-sky-blue;
  background-image: url("../../img/svg/bg-form.svg");
  background-repeat: no-repeat;
  background-size: cover;
  color: $color-default-white;

  @include vp-1023 {
    background-image: none;
  }
}

.feedback__container {
  background-image: url("../../img/paper-airplane.png");
  background-size: 510px 285px;
  background-repeat: no-repeat;
  background-position: 0 246px;
  padding: 165px 0 250px 0;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../../img/paper-airplane@2.png");
  }

  @include vp-1439 {
    padding: 165px 32px 250px;
  }

  @include vp-1023 {
    padding-top: 45px;
    padding-bottom: 124px;
    background-image: none;
  }

  @include vp-767 {
    padding: 48px 25px 89px;
  }
}

.feedback__form {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-right: 10px;
  width: 48.7%;
  margin-left: auto;

  @include vp-1023 {
    width: 100%;
    padding-right: 0;
  }
}

.feedback__inputs-wrapper {
  margin-left: 5px;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 16px;

  @include vp-1023 {
    margin-left: 0;
    margin-bottom: 15px;
  }

  @include vp-767 {
    margin-bottom: 11px;
  }
}

.feedback__label {
  width: 47.5%;
  margin-bottom: 20px;

  @include vp-767 {
    width: 100%;
  }
}

.feedback__label--quest {
  width: 100%;
  height: 155px;

  @include vp-767 {
    height: 180px;
  }
}
